const Navbar = ({userLogin,userLogout, user}) => {
    const loginPath = `${config.mp_api_uri}/oauth/authorize/?app_id=${config.app_id}/&redirect_uri=${config.redirect_uri}`;
    const registerPath = `${config.mp_browser_uri}/register`;
    return (
        <div className='navbar'>
          <div className='navbar__brand'>
            <Logo className="logo" />
          </div>
          { !user ?
            <div className="navbar__form">
                <a onClick={userLogin} href={loginPath}> LOGIN </a>
                 
                <a href={registerPath}> REGISTER </a>
            </div>
          :
            <NavbarDropdown user={user} userLogout={userLogout} />
          }
        </div>
    )
}
export default Navbar;


